.workDoneContainer{
    min-width: 320px;
    max-width: 320px;
    max-height: 320px;
    min-height: 320px;
    background-color:'#77b6b6';
    border-top-left-radius: 10%;
    border-bottom-right-radius: 10%;
    padding:30px;
    text-align: center;
    margin-left: 4rem;
    margin-bottom: 4rem;
    color: black;
}
@media only screen and (max-width: 768px) {
    .workDoneContainer{
        margin-left: 3rem;
    }
}
.outerSquare{
    border-color:  white;
    height: -webkit-fill-available;
    width:-webkit-fill-available;
}
.Try{
    border-radius: 58px;
    -webkit-box-shadow: 0 6px;
            box-shadow: 0 6px;
    outline: none !important;
}


